.card-container {
    perspective: 1000px;
  }
  
  .card {
    width: 300px;
    height: 300px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.6s;
  }
  
  .card.flipped {
    transform: rotateY(180deg);
    width: 300px;
    height: 300px;
  }
  
  .card-front,
  .card-back {
    position: absolute;
    width: 300px;
    height: 300px;
    backface-visibility: hidden;
    cursor: pointer;
  }
  
  .card-back {
    transform: rotateY(180deg);
  }
  